import styled from "styled-components";
import gClef from "../../assets/img/g-clef.png";
import wholeNote from "../../assets/img/whole-note.png";

const Wrapper = styled.div`
  position: relative;
  margin-top: 40px;
  margin-bottom: 40px;
`;

const Staff = styled.div`
  width: 500px;
`;

const Line = styled.div`
  height: 1px;
  margin-bottom: 14px;
  background-color: #000;
`;

const Gclef = styled.img`
  position: absolute;
  top: -25px;
  left: 0;
  height: 116px;
`;

const WholeNote = styled.img`
  position: absolute;
  top: 1px;
  left: 0;
  height: 14px;
`;

const NoteStartPosition = {
  "clef": {
    "g": {
      "notes": {
        "C": {
          "position": {
            "x": 155,
            "y": 68
          },
          "guideline": true
        },
        "D": {
          "position": {
            "x": 155,
            "y": 61
          },
          "guideline": false
        },
        "E": {
          "position": {
            "x": 155,
            "y": 54
          },
          "guideline": false
        },
        "F": {
          "position": {
            "x": 155,
            "y": 47
          },
          "guideline": false
        },
        "G": {
          "position": {
            "x": 155,
            "y": 40
          },
          "guideline": false
        },
        "A": {
          "position": {
            "x": 155,
            "y": 33
          },
          "guideline": false
        },
        "B": {
          "position": {
            "x": 155,
            "y": 26
          }
        }
      }
    }
  }
}

export default class MusicStaff extends React.Component {
  render() {
    return (
      <Wrapper>
        <WholeNote src={wholeNote} />
        <Gclef src={gClef} />
        <Staff>
          <Line />
          <Line />
          <Line />
          <Line />
          <Line />
        </Staff>
      </Wrapper>
    );
  }
}
